	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<h1>AAA Poured Walls</h1>\n<p>AAA is a concrete pouring company who specializes in custom stonework. Their services encompass poured walls, flat work, and decorative concrete. I had a lot of fun with this design and feel it is one of my better designs. The client was happy to showcase their new website, and they were even happier to be able to manage it themselves.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/AAA-c760d065b0a24a8dd590878f72933220-9e795.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 159.69230769230768%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"AAA Homepage Image\"\n        title=\"\"\n        src=\"/static/AAA-c760d065b0a24a8dd590878f72933220-f8fb9.jpg\"\n        srcset=\"/static/AAA-c760d065b0a24a8dd590878f72933220-e8976.jpg 148w,\n/static/AAA-c760d065b0a24a8dd590878f72933220-63df2.jpg 295w,\n/static/AAA-c760d065b0a24a8dd590878f72933220-f8fb9.jpg 590w,\n/static/AAA-c760d065b0a24a8dd590878f72933220-9e795.jpg 650w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/aaa","title":"AAA","featuredText":"AAA is a concrete pouring company who specializes in custom stonework.","featuredImage":{"childImageSharp":{"responsiveSizes":{"src":"/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-e2aa3.jpg","srcSet":"/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-09d40.jpg 100w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-7756f.jpg 200w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-e2aa3.jpg 400w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-45840.jpg 600w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-c899b.jpg 650w","sizes":"(max-width: 400px) 100vw, 400px"}}}}}},{"node":{"html":"<h1>Van’s Car Wash</h1>\n<p>Van’s Car Wash is a local car wash chain serving seven locations across the West Michigan area. Before Vans came to us he had no website. The main objective of the site was to provide the Google location for each car wash across the area, so that’s what I did. The website has all locations and Google iFrames for easy navigation. The biggest priority for the site was for it to be mobile friendly. Most of the users who would be viewing the site would be on their mobile devices.</p>\n<h2>Design</h2>\n<p>I had a lot of fun designing this site and feel that it is clean and sharp. The white background with hints of blue keep the content clean and specific to the content. The mock up was created in Photoshop and once the design was approved I moved to development.</p>\n<h2>Development</h2>\n<p>The site was developed in WordPress. No theme was used to create this site. I used a blank template and started from scratch. There was no blog or post types, so development of the site was primarily kept to HTML, CSS, and Javascript. By using Advances Custom Fields plugin, I was able to make it so the owner could easily come in and swap the coupon pictures out. The purpose of a CMS is to bring ease of use to the client and that is just what Van’s Car Wash does.</p>","frontmatter":{"path":"/vans-carwash","title":"Vans Carwash","featuredText":"Van’s Car Wash is a local car wash chain serving seven locations across the West Michigan area.","featuredImage":{"childImageSharp":{"responsiveSizes":{"src":"/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-e2aa3.jpg","srcSet":"/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-09d40.jpg 100w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-7756f.jpg 200w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-e2aa3.jpg 400w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-45840.jpg 600w,\n/static/AAA_Featured-9def1918dde01e995570d8e9f89670f8-c899b.jpg 650w","sizes":"(max-width: 400px) 100vw, 400px"}}}}}}]}},"pathContext":{}}

